import * as React from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';

import Staff from '@miq/staffjs';
import { Response } from '@miq/utiljs';
import { useRequest } from '@miq/hookjs';
import { Table, Pagination, ViewSection, Icons } from '@miq/componentjs';

import { TCategory } from '../types';
import { PublishedIcon } from '../components';

import { StaffCategory } from './models';
import { CategoryCreateForm, CategoryUpdateForm } from './forms';
import Form, { useForm } from '@miq/formjs';

export default function CategoryStaffIndexView() {
  const navigate = useNavigate();
  const [params] = useSearchParams();
  const page = params.get('page');

  const { res, updateItem, refresh } = useRequest(() => StaffCategory().list({ params: { page } }), {
    refreshDeps: [page],
  });

  const [isAdding, setAdding] = React.useState(false);

  const r = new Response(res);
  if (!r.isSuccess) return null;

  return (
    <Staff.View
      title="Catégories"
      back="/staff/store/"
      actions={
        <Staff.Button
          onClick={() => setAdding(!isAdding)}
          className="btn-primary-3"
          title="Add a new category"
          requiredPerms={['store.add_category']}
        >
          Ajouter
        </Staff.Button>
      }
      requiredPerms={['store.view_category']}
    >
      {isAdding && (
        <div className="miq-container center">
          <Staff.Section title="Add a category" requiredPerms={['store.add_category']} border>
            <CategoryCreateForm onSuccess={({ data }: any) => navigate(data.slug)} />
          </Staff.Section>
        </div>
      )}

      {r.items.length === 0 && !isAdding && (
        <div className="text-center p-staff-list-empty py-3 my-3">
          <p className="fw-light text-md">Ajouter une catégorie pour commencer</p>

          <div className="my-4">
            <Staff.Button
              onClick={() => setAdding(!isAdding)}
              requiredPerms={['store.add_category']}
              className="btn btn-primary btn-md"
            >
              Add a category
            </Staff.Button>
          </div>
        </div>
      )}

      {r.items
        .sort((a: TCategory, b: TCategory) => (a.position! > b.position! ? 1 : -1))
        .map((item: TCategory) => (
          <CategoryStaffListItem
            item={item}
            onUpdate={(r) => {
              updateItem(r.data);
            }}
            key={item.slug}
          />
        ))}
    </Staff.View>
  );
}

const CategoryStaffListItem = ({ item, ...props }: { item: TCategory; onUpdate: (payload: any) => void }) => {
  const [open, setOpen] = React.useState(false);
  const instance = StaffCategory(item);

  const form = useForm({
    name: instance?.name || '',
    description: instance?.description || '',
    position: instance?.position || 0,
  });

  return (
    <ViewSection
      border
      title={
        <div onClick={() => setOpen(!open)} className="d-flex align-items-center">
          <div style={{ width: 48, height: 48 }}>
            <Staff.Img.Square slug="slug" src="src" {...instance.cover_data} className="rounded" />
          </div>

          <div className="ms-1">
            <div className="text-ellipsis">
              <span className="me-1">{form.values.name}</span>

              <PublishedIcon show={instance.is_published} />
            </div>

            <div className="text-sm">
              <span>Total: {instance.products_count} </span>
              <span>
                ({instance.published_count}/<span className="text-muted">{instance.draft_count}</span>)
              </span>
            </div>
          </div>
        </div>
      }
      actions={
        <div>
          <Staff.Link
            to={`${instance.slug}/`}
            className="btn btn-square btn-primary-3"
            requiredPerms={['store.change_category']}
          >
            <Icons.PencilSquare />
          </Staff.Link>
        </div>
      }
      className="mb-1"
    >
      {open && (
        <div className="">
          <CategoryUpdateForm
            context={form}
            instance={instance}
            fields={['name', 'description', 'position']}
            onSuccess={props.onUpdate}
          >
            <div className="d-flex justify-content-center">
              <Form.Submit value="Sauvegarder" className="btn-primary-3 min-w-25" />
            </div>
          </CategoryUpdateForm>
        </div>
      )}
    </ViewSection>
  );
};

export * from './models';
