import { StaffModel, StaffService } from '@miq/staffjs';
import { TCategory } from '../types';

class StaffCategoryService extends StaffService<TCategory> {
  publish() {
    return this.update({ is_published: true });
  }

  unpublish() {
    return this.update({ is_published: false });
  }
}

export const StaffCategory = (data: TCategory = {}) => {
  let { dt_published } = data;
  if (dt_published) dt_published = new Date(dt_published);

  return StaffModel<TCategory, StaffCategoryService>({ ...data, dt_published }, 'categories', StaffCategoryService);
};
