import Form, { DescriptionField, NameField, TextFieldProps, useForm } from '@miq/formjs';
import { FormProps, IFormCallbackProps, IFormUpdateProps } from '@miq/formjs/src/Form';
import { isRequired as isR } from '@miq/utiljs';
import React from 'react';
import { StaffCategory } from './models';

export const CategoryCreateForm: React.FC<IFormCallbackProps> = (props) => {
  const form = useForm({ name: '' });

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();

    return StaffCategory()
      .create(form.values)
      .then((res: any) => props?.onSuccess?.(res))
      .catch((err: any) => {
        form.handleError(err);
        props?.onError?.(err);
      });
  };
  return (
    <CategoryForm context={form} onSubmit={handleSubmit}>
      <div className="">
        <CategoryForm.NameField error={form.errors.name} />
      </div>

      <div className="my-3">
        <Form.Submit value={'Save category'} className="btn btn-primary" />
      </div>
    </CategoryForm>
  );
};

interface ICategoryUpdateFormProps extends Omit<IFormUpdateProps, 'instance'> {
  instance: ReturnType<typeof StaffCategory>;
  onWillSubmit?: () => void;
  fields: string[];
}

export const CategoryUpdateForm: React.FC<ICategoryUpdateFormProps> = ({
  instance = isR('Category instance'),
  fields = [],
  children,
  ...props
}) => {
  const { onSuccess, onError, onWillSubmit, ...rest } = props;
  const { context } = rest;

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    onWillSubmit?.();

    const fd: any = {};
    fields.forEach((field) => {
      const value = context.values[field];
      if (value == null) return;
      fd[field] = value;
    });

    return instance
      .update(fd)
      .then((res: any) => onSuccess?.(res))
      .catch((err: any) => {
        if (onError) return onError(err);
        return context?.handleError?.(err);
      });
  };
  return (
    <CategoryForm {...rest} onSubmit={handleSubmit}>
      {fields.includes('name') && (
        <CategoryForm.NameField
          label="Nom de la catégorie"
          error={context?.errors?.name}
          text={`Ajoutez un nom qui décrit cette catégorie.\nNous recommandons 65 caractères au max.`}
        />
      )}

      {fields.includes('description') && (
        <CategoryForm.DescriptionField
          label="Description"
          error={context?.errors?.description}
          text={`Décrivez cet catégorie.`}
        />
      )}

      {fields.includes('position') && (
        <CategoryForm.PositionField
          label="Position"
          error={context?.errors?.position}
          // text={`Décrivez cet ca`}
        />
      )}
      {children}
    </CategoryForm>
  );
};

export const CategoryForm = (props: FormProps) => <Form {...props} />;

CategoryForm.NameField = (props: TextFieldProps) => (
  <NameField placeholder="Entrez le nom de la catégorie ..." {...props} required />
);

CategoryForm.DescriptionField = (props: TextFieldProps) => <DescriptionField {...props} />;
CategoryForm.PositionField = ({ label, error, ...props }: TextFieldProps) => (
  <Form.Field {...{ label, error }}>
    <Form.Text name="position" type="number" min={0} {...props} />
  </Form.Field>
);
